@import "../../App.scss";
.navbar {
  height: 8vh;
  position: fixed;
  z-index: 2;
  width: 100%;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
  transition: 0.3s all ease;
}

.links{
    display: flex;
    align-items: center;
    height: 100%;
    max-width: 1120px;
    margin: auto;
}

.nav-link {
  padding: 0px 1em;
  margin: 0px 1em;
  outline: 0;
  font-weight: 900;
  font-size: 20px;
  color: $primary-color-blue;
  padding: 0.8em 0.8em;
  transition: all 0.3s ease;
  cursor: pointer;
  text-decoration: none;
  text-transform: lowercase;
  &:first-of-type {
    margin-right: auto;
  }
  &:after {
    content: "";
    width: 0%;
    display: block;
    padding: 0.2em 0;
    border-bottom: solid 2px $accent;
    transition: all 0.3s ease;
  }
  &:hover {
    &:after {
      width: 100%;
    }
  }
}

.navbar-filler {
  padding-top: 50px;
  height: 8vh;
}

.at-top {
  height: 10vh;
  background-color: transparent;
  box-shadow: none;
}
